
export default  {
    state: {
        modalMode: false,
    },
    mutations: { /***** USING GLOBAL ****/
        SET_MODAL_MODE_ON(state) { state.modalMode = true },
        SET_MODAL_MODE_OFF(state) { state.modalMode = false },
    },
    getters: {
        items({state, getters}, arg2, arg3, modulesGetters, ) {
            //console.warn(arguments)
            const admin = modulesGetters['login/isAdmin']
            const user = modulesGetters['login/isUser']
            const loggedIn = admin || user
            return [
                {title: '',                to:'/forum',            slug: 'forum',              icon:'question_answer',     show: true,         roles: ['*']},
                {title: ' ',         to:'/ask',              slug: 'ask',                icon: 'plus_one',           show: loggedIn,     roles: ['admin', 'user'] },
                {title: ' ',   to:'/edit-categories',  slug: 'edit-categories',    icon: 'bookmarks',          show: loggedIn,     roles: ['admin'] },
                {title: '', icon: 'person',                        show: !loggedIn,    roles: ['guest'] },
                {title: '', icon: 'exit_to_app',                   show: loggedIn,     roles: ['admin','user'] },
            ]
        }

    }

}
